
body {
  margin: 0;
}
  
.star {
  display: flex;
}
  
.stars__star {
  height: 15px;
  width: 15px;
  margin-right: 4px;
  background: no-repeat center url(images/star.svg);
}
  
.stars--1 :first-child,
.stars--2 :nth-child(-n+2),
.stars--3 :nth-child(-n+3),
.stars--4 :nth-child(-n+4),
.stars--5 :nth-child(-n+5) {
  background-image: url(images/star-active.svg);
}
